    // }
    // span:nth-child(3){
    //     filter: blur(0px);
    //     animation: blur-text 1.5s 0.4s infinite linear alternate;
    // }
    // span:nth-child(4){
    //     filter: blur(0px);
    //     animation: blur-text 1.5s 0.6s infinite linear alternate;
    // }
    // span:nth-child(5){
    //     filter: blur(0px);
    //     animation: blur-text 1.5s 0.8s infinite linear alternate;
    // }
    // span:nth-child(6){
    //     filter: blur(0px);
    //     animation: blur-text 1.5s 1s infinite linear alternate;
    // }
    // span:nth-child(7){
    //     filter: blur(0px);
    //     animation: blur-text 1.5s 1.2s infinite linear alternate;
    // }
}

@keyframes blur-text {
    0% {
        filter: blur(0px);
    }
    100% {
        filter: blur(4px);
    }
}
body.light{background: var(--light_bg);
    #loading.loading__active{
        background: var(--light_bg);
        .loading__text span{
            color: var(--black);
        }
    }
}

#loading.loading__active.light{
    background: var(--light_bg);
    .loading__text span{
        color: var(--black);
    }
}